import React, { Component } from'react'
import Statistics from '../Statistics/Statistics';
import Section from '../Section/Section';
import FeedbackOptions from '../Feedback/FeedbackOptions';
import Notification from '../Notification/Notification';

class Feedback extends Component{
    state = {
        good: 0,
        neutral: 0,
        bad: 0,
        vibilde: false
      }

      show = () => {
          this.setState({visible: true})
      }

      hide = () => {
          this.setState({visible:false})
      }

      buttonGood = () => {
        this.setState(prevState =>{
            return {
                good: prevState.good + 1,
            }
        })
      }
      buttonNeutral= () =>{
         this.setState(prevState =>{
             return {
                 neutral: prevState.neutral + 1,
             }
         })
       }
       buttonBad = () =>{
         this.setState(prevState =>{
             return {
                 bad: prevState.bad + 1,
             }
         })
       }

    render(){
        return(
            <>
            <Section title="Please leave feedback"></Section>

            <FeedbackOptions 
            onButtonGood={this.buttonGood} 
            onButtonNeutral={this.buttonNeutral} 
            onButtonBad={this.buttonBad}
            onShow = {this.show}
            />

            <Section title="Statictics"></Section>

            {this.state.visible &&
            <Statistics 
            good={this.state.good} 
            neutral={this.state.neutral} 
            bad={this.state.bad} 
            total={`${this.state.good + this.state.neutral + this.state.bad}`} 
            positivePercentage={`${Math.round((this.state.good/(this.state.good + this.state.bad)) * 100).toFixed(0)}`}>
            </Statistics> 
            }
            
            {!this.state.visible &&
            <Notification message="No feedback given"></Notification>}
        </>
        )
    }
}

export default Feedback;